import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { connect } from "react-redux";
import { convLang } from "../actions/rootAct";
import Admin from "./admin/Admin";
import Main from "./Main";

class App extends Component {
  render() {
    const { rootData, convertLang } = this.props;
    return (
      <Router>
        <Route
          render={({ location }) => {
            if (!/^\/admin/.test(location.pathname)) {
              return (
                <Main rootData={rootData} location={location} convertLang={convertLang} />
              );
            } else {
              return (
                <Admin rootData={rootData} />
              )
            }
          }}
        /></Router>
    );
  }
}

const mapState = state => {
  return { rootData: state };
};

const mapDispatch = dispatch => {
  return {
    convertLang: lang => dispatch(convLang(lang))
  };
};

export default connect(
  mapState,
  mapDispatch
)(App);
