import React, { useState } from "react";

function Login(props) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showAlert, setShowAlert] = useState(false);
  const [alertMsg, setAlertMsg] = useState('');

  const usernameInput = React.createRef();
  const passwordInput = React.createRef();

  function handleReset() {
    setUsername('');
    setPassword('');
    usernameInput.current.focus();
    setShowAlert(false);
  }

  function handleSubmit(e) {
    e.preventDefault()
    if (!username) {
      usernameInput.current.focus();
      setAlertMsg('Username cannot be empty!')
    }
    else if (!password) {
      passwordInput.current.focus();
      setAlertMsg('Password cannot be empty!')
    } else if (username === 'arfan' && password === 'ganteng') {
      props.history.push('/admin')
    }
    else {
      setAlertMsg('Username or password is wrong!')
    }
    setShowAlert(true)
  }

  function toggleViewPass() {
    const pass = document.getElementById('password');
    if (pass.type === 'password') {
      pass.type = 'text';
    } else {
      pass.type = 'password';
    }
  }

  return (
    <div id="login-container">
      <div className="row">
        <div id="login-bg"></div>
        <div className="col-12">
          <div className="col-lg-4 offset-lg-4 col-md-6 offset-md-3 col-sm-8 offset-sm-2">
            <div id='login-form'>
              <h2 className="login-title">Login Form</h2>
              <form>
                <div className="form-group">
                  <label htmlFor="username">
                    Username
                </label>
                  <input id="username" ref={usernameInput} className="form-control" value={username} onChange={(e) => setUsername(e.target.value)} placeholder="Enter username" autoFocus />
                </div>
                <div className="form-group">
                  <label htmlFor="password">
                    Password
                  </label>
                  <div className="input-group">
                    <input type="password" ref={passwordInput} id="password" className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Enter password" />
                    <div className="input-group-append">
                      <div className="input-group-text" onClick={toggleViewPass}>
                        <span className="fa fa-eye"></span>
                      </div>
                    </div>
                  </div>
                </div>
                {showAlert && <div className="alert alert-danger">{alertMsg}</div>}
                <div className="login-button">
                  <input type="reset" className="btn btn-secondary" value="Reset" onClick={handleReset} />
                  <input type="submit" className="btn" value="Submit" onClick={handleSubmit} />
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
